body{
    background-color: #114f39;
}

.App{
    background-color: #097948;
    min-height: 500px;
    width: 400px;
    margin: 50px auto;
}

#to-do-form input{
    background-color: #733efb;
    border: 0;
    width: 200px;
    height: 50px;
    padding: 0 20px;
    margin: 20px;
    font-size: 18px;
    border-radius: 10px;
    color: #500425;
}

#to-do-form input::placeholder {
        color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:focus{
    outline: none;
}

#to-do-form button{
    height: 50px;
    width: 80px;
    border-radius: 5px;
    border: 0;
    background-color: #db0505;
    cursor: pointer;
    font-weight: bolder;
    color: #ffffff;
}

#to-do-form button:focus{
    outline: none;
}